.header {
  font-family: "Lato", sans-serif;
  color: var(--text-primary);
  padding-top: 1em;
  margin-bottom: 0.7em;
}
.header h1 {
  font-size: 1.2rem;
  font-weight: 700;
  word-spacing: 3px;
  text-transform: uppercase;
  text-decoration: underline;
}

/*
  MEDIA QUERIES
*/

@media (min-width: 600px) {
  .header {
    padding-top: 2em;
    margin-bottom: 2em;
  }
  .header h1 {
    font-size: 2.1em;
    text-transform: capitalize;
    letter-spacing: 1px;
  }
}
.titleheader {
  width: 100%;
  color: var(--text);
  padding-top: 0.53rem;
  padding-bottom: 0.43rem;
  cursor: pointer;
  border-radius: 5px 5px 0 0;
}
.titleheader .text-wrapper {
  display: inline-block;
  padding-left: 1em;
}
.titleheader h2 {
  font-size: 0.92em;
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  margin: 0;
  padding: 0;
  letter-spacing: 1.1px;
  text-transform: uppercase;
}
.titleheader-editor {
  background-color: var(--toolbar-one);
}
.titleheader-previewer {
  background-color: var(--toolbar-two);
}

/*
  MEDIA QUERIES
*/

@media (min-width: 600px) {
  .titleheader .text-wrapper .title {
    font-size: 1.12em;
    padding-top: 0.3em;
    padding-bottom: 0.3em;
  }
}
@media (min-width: 1200px) {
  .titleheader .text-wrapper .title {
    font-size: 1.22em;
  }
}

.editor {
  height: 250px;
  width: 100%;
  font-size: 0.95em;
  font-family: "Lato";
  font-weight: 400;
  letter-spacing: 0.05em;
  transition: 0.37s;
  padding-left: 1em;
  padding-right: 0.3em;
  margin-bottom: 3em;
  background: var(--text);
  color: var(--primary-text);
  box-shadow: 0px 10px 12px 0px rgba(0,0,0,0.59);
  overflow: auto;
  border: 0;
  resize: none;
  margin-bottom: 30px;
}
.editor:focus {
  outline:none;
}

/*
  MEDIA QUERIES
*/

@media (min-width: 600px) {
  .editor {
    height: 450px;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .editor {
    height: 600px;
    font-size: 1em;
  }
}
.previewer {
  height: 250px;
  width: 100%;
  font-size: 0.95em;
  font-family: "Lato";
  font-weight: 400;
  letter-spacing: 0.01em;
  margin-bottom: 5px;
  transition: 0.37s;
  padding: 1em;
  margin-bottom: 2em;
  background: var(--text);
  color: var(--primary-text);
  box-shadow: 0px 10px 12px 0px rgba(0,0,0,0.59);
  overflow: auto;
  z-index: 2;
}

/* 
  Previewer element's styles 
*/
.previewer code {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  letter-spacing: -0.03em;
  background-color: var(--divider);
  color: var(--text-primary);
  padding: 0 0.35em;
  border-radius: 2px;
  margin-top: 3px;
}
.previewer pre {
  background-color: var(--text-primary);
  color: var(--text);
  padding: 1em 0 1em 1em;
  font-family: 'Lato', sans-serif;
  border-radius: 5px;
}
.previewer pre code {
  padding: 0;
  color: inherit;
  background-color: inherit;
  font-weight: 300;
  letter-spacing: 0.05em;
  line-height: 1.65;
}
.previewer table {
  width: 100%;
  margin: 1em 0;
}
.previewer th {
  background-color: #d2d7da;
  padding: 0.2em 0.7em;
  border: 1px solid var(--divider);
  font-weight: 700;
}
.previewer td {
  padding: 0.1em 0.7em;
  border: 1px solid var(--divider);
  font-weight: 400;
}
.previewer blockquote {
  font-weight: 300;
  font-style: italic;
  padding: 0.9em 0 0.9rem 1em;
  background-color: var(--divider);
  border-radius: 3px
}
.previewer blockquote p {
  margin: 0;
}

/* 
  MEDIA QUERIES 
*/

@media (min-width: 600px) {
  .previewer {
    height: 450px;
    margin-bottom: 5px;
  }
}
@media (min-width: 1200px) {
  .previewer {
    height: 600px;
    font-size: 1em;
  }
}

.footer {
  display: block;
  z-index: 1;
  padding: 0;
  font-weight: 300;
  font-size: 1em;
  font-family: 'Lato', sans-serif;
}
.footer span {
  display: inline-block;
  padding: 4px 9px 4px 9px;
  background: var(--text);
  border-top-left-radius: 5px;
}
.footer a {
  display: inline-block;
  color: inherit;
  transition: 0.65s;
  padding: 4px 9px 4px 9px;
  border-bottom-right-radius: 5px;
  background: var(--text-primary);
  color: var(--divider);
}
.footer a:hover {
  color: var(--divider);
  text-decoration: underline;
}

.container {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  z-index: 1;
}
.row {
  flex: 1 1 auto;
  flex-direction: row;
  z-index: inherit;
}
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 300;
  src: local('Lato'), url(../../static/media/Lato-Light.5b761f2d.ttf) format('truetype');
}
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  src: local('Lato'), url(../../static/media/Lato-Regular.7f690e50.ttf) format('truetype');
}
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 700;
  src: local('Lato'), url(../../static/media/Lato-Bold.44dfe8cc.ttf) format('truetype');
}

html, body, div, span {
  margin: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
:root {
  --back-one: #b360d1;
  --back-two: #94abed;
  --toolbar-one: #751CC9;
  --toolbar-two: #2e294e;
  --text: #f4f4f6;
  --divider: #dadada;
  --text-primary: #262730;
}

* {
  box-sizing: border-box;
}
body {
  height: 100vh;
  font-family: unset;
  background: linear-gradient( -90deg, 
    #b360d1,
    #94abed);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* Bootstrap resetters */
.row {
  padding-left: 15px;
  padding-right: 15px;
}

